    <div class="container">
      <h1 class="mt-5">A decentralised rating system for building trustless reputation systems<br>Status: Request For Comments</h1>
      <hr>
      <div class="row">
        <div class="col">
          <p>
            This page describes a simple reputation system built on Bitcoin Cash. This includes a spec for the system, and a live tutorial (WIP)<br>
            This system was inspired by the <a href="https://drive.google.com/file/d/1SnRdVeGoPkkdk2lXtdjDfy7c0z1ZlmRE/view">Freedom Project</a>. As such, it's meant to be modular and interoperable. <br>
            A rough JavaScript/Bitbox implmentation resides <a href="https://github.com/mtrycz/bchrep/">here</a>.
          </p>
        </div>
      </div>

      <hr>

      <div class="row">
        <div class="col">
          <h2>Rationale</h2>
          <p>A simple <strong>rating system</strong> is presented. A <strong>reputation system</strong> can easily be built upon the rating system.</p>
          <p>Many reputation systems are tied to the platforms (and third parties) they reside on and they build on proprietary data (ex. a user can't bring their reputation from Booking to AirBnb). The presented system is meant to be trustless and interoperable: the users own their digital identities, the rating data is forever public.</p>
          <p>Many reputation systems are overly complicated because they seek to provide value to who runs them. The presented system is meant to provide value to participants, but can't be directly monetized (ie. the author can't make more money out of this than anyone else).</p>
          <p>When building upon the rating system, anyone can freely build their own reputation service, weather for personal use or as a service provider. An example of a (very simple) reputation function is presented.<br>
          A reputation service provider can make their aggregation function public (to be fully auditable), or add their secret sauce while providing value to its users. Either way, they don't own their data.</p>
          <p>NB: This system is not mean to provide reputation for social networks (ex. Twitter, Reddit, ...). The basic unit of interaction eligible for rating is an economic transaction. As such this system is more fitting for economic platforms (Eg. Bay, Uber, ...). </p>
        </div>
      </div>

      <hr>

      <div class="row">
        <div class="col">
          <h2>
            Spec
          </h2>
          <p>Two <strong>digital identities</strong> that <strong>transact</strong> onchain, can <strong>rate</strong> their interaction. A combination an identity's recieved ratings amount to its <strong>reputation</strong>.</p>

          <h4>Registration</h4>
          <p>
            A digital identity is simply a public address on the BCH network. A user registers for the reputation system by proving the ownership of the private key to that address. This amounts to saying "I hereby claim to own the address [...], and here is the proof: [...]".<br>
            The registration action is an OP_RETURN transaction with 
            <ul>
              <li>
                <a href="https://github.com/bitcoincashorg/bitcoincash.org/blob/master/spec/op_return-prefix-guideline.md">Prefix version</a> 
                &nbsp;<code>0abcde1d</code> (provvisional)
              </li>
              <li>
                The Hash160 representation of the address to be registered as the digital identity<br>
                (Optional: the address can be bound to a <a href="https://www.cashaccount.info/">CashAccount</a> or any other identity scheme. It's intended to be interoperable).
              </li>
              <li>
                The signature of the address, made with the private key and the address itself.
              </li>
            </ul>
          </p>

          <p>The participation in the reputation system is entirely voluntary.<br> The identities are disposable, anyone can register as many as they want.</p>

          <b-button v-b-toggle.collapse-1 variant="info">Show example</b-button>

          <b-collapse id="collapse-1" class="alert alert-secondary" role="alert">
            <b-card>
              <a href="https://explorer.bitcoin.com/bch/tx/e1e0a657e00193fc150ccd35ce4d8325c31e2a3c05bfa49bd9844c964e2f94ff">Here</a>
              You can see an example of a registration transaction for the address <code>bitcoincash:qrqtx0hj9ghfkp8qzw4rvuhlyuk33cqqsqkkls5cu0</code>, which OP_RETURN structure is as follows:
              <ul>
                <li>
                  <code>OP_RETURN</code>
                </li>
                <li>
                  <code>0abcde1d</code> - prefix version of a registering transaction (provvisional)
                </li>
                <li>
                  <code>c0b33ef22a2e9b04e013aa3672ff272d18e00080</code> - Hash160 of the address 
                </li>
                <li>
                  <code>20f567dc52d5c53f09c76eb23ac31c4e90c42122382762bd591becfbe6cd1d325a5d145cf0f9e49edad8fe571a7bd78a0d39a946b46860d2f1157b1a88362092d5</code> - signature of the above (without OP_RETURN and version prefix) in hex (translates to <code>IPVn3FLVxT8Jx26yOsMcTpDEISI4J2K9WRvs++bNHTJaXRRc8PnkntrY/lcae9eKDTmpRrRoYNLxFXsaiDYgktU=</code>).
                </li>
              </ul>
              Go ahead a check the signature in Electron Cash, or your preferred software. 
            </b-card>
          </b-collapse>

          <hr>

          <h4>Transaction</h4>
          <p>
            If two digital identities agree to trade, they complete a transaction onchain from one identity to the other (ie. from one bitcoincash address to the other).<br>
            A transaction made between two digital identities is eligible for rating from both identities.<br>
          </p>
          <p><a href="https://explorer.bitcoin.com/bch/tx/23e2a31495ab30e5f99e647c45c3b8b0201531918eda18296cf5c42bbf9b4553">Here</a> you can see a transaction between two registered digital identities.

          <hr>

          <h4>Rating</h4>
          <p>
            After the identities have engaged in trade, they can rate their interaction/transaction.<br>
            Each party can rate the transaction only once, and they can do so within a timeframe.<br>
            A rating is an onchain transaction with an OP_RETURN output with
            <ul>
              <li>
                Prefix version <code>0abcde1f</code> (Provvisional)
              </li>
              <li>
                Hash160 representation of the address of the identity that is being rated
              </li>
              <li>
                Hash160 representation of the address of the identity that is rating
              </li>
              <li>
                Transactionid of the interaction between the identities
              </li>
              <li>
                Rating expressed as an integer in range 0 to 99
              </li>
              <li>
                Signature of all of the above, signed by the identity that is rating
              </li>
            </ul>
          </p>

          <b-button v-b-toggle.collapse-2 variant="info">Show example</b-button>

          <b-collapse id="collapse-2" class="alert alert-secondary" role="alert">
            <b-card>
              <a href="https://explorer.bitcoin.com/bch/tx/85e8a31f148fef6e7fdbf7685cd78b7cc5c8e58df3340bde1c246b2a5ac82ecf">Here</a>
              You can see an example of a rating of the transaction <code>23e2a31495ab30e5f99e647c45c3b8b0201531918eda18296cf5c42bbf9b4553</code>, that involved <code>bitcoincash:qqqdfw4md8zan9wf52809ycv7stlgrcycusmcc3drh</code> and <code>bitcoincash:qrqtx0hj9ghfkp8qzw4rvuhlyuk33cqqsqkkls5cu0</code>, which OP_RETURN structure is as follows:
              <ul>
                <li>
                  <code>OP_RETURN</code>
                </li>
                <li>
                  <code>0abcde1f</code> - prefix version of a rating transaction
                </li>
                <li>
                  <code>00d4babb69c5d995c9a28ef2930cf417f40f04c7</code> - Hash160 of the digital identity that is being rated (<code>bitcoincash:qqqdfw4md8zan9wf52809ycv7stlgrcycusmcc3drh</code>)
                </li>
                <li>
                  <code>c0b33ef22a2e9b04e013aa3672ff272d18e00080</code> - Hash160 of the digital identity that is rating (<code>bitcoincash:qrqtx0hj9ghfkp8qzw4rvuhlyuk33cqqsqkkls5cu0</code>)
                </li>
                <li>
                  <code>23e2a31495ab30e5f99e647c45c3b8b0201531918eda18296cf5c42bbf9b4553</code> - Transaction id of the trade between them
                </li>
                <li>
                  <code>99</code> - the actual rating
                </li>
                <li>
                  <code>1f6ee301b3ee5b4e268a1a22b189a8546b100e4185697117d209ea8ca874053ec73c4c478e08c79b2d5cb291d308b8cfd1c06c23e12061024ab152e2a69039829e</code> - signature of all of the above (without OP_RETURN and version prefix) in hex (translates to <code>H27jAbPuW04mihoisYmoVGsQDkGFaXEX0gnqjKh0BT7HPExHjgjHmy1cspHTCLjP0cBsI+EgYQJKsVLippA5gp4=</code>).
                </li>
              </ul>
              Go ahead a check the signature in Electron Cash, or your preferred software (the "message" is the concatenation of the above strings without spaces, and the "address" is the cashaddress of the rating indentity).
            </b-card>
          </b-collapse>
          
          <hr>

          <h2>Reputation</h2>
          <p>
            A <strong>reputation</strong> of a digital identity can be computed as a function of the individual ratings.<br>
            Since the ratings are public, anyone can define and run their own function, but it's probable that dedicated reputation services will arise.<br>
            The reputation system is intentionally independent, but interoperable with the rating system. 
          </p>

          <b-button v-b-toggle.collapse-3 variant="info">Show example</b-button>

          <b-collapse id="collapse-3" class="alert alert-secondary" role="alert">
            <b-card>
              <a href="https://bitdb.bitcoin.com/explorer/ewogICJ2IjogMywKICAicSI6IHsKICAgICJmaW5kIjogewogICAgICAib3V0LmIxIjogewogICAgICAgICIkcmVnZXgiOiAiXkNyemVId0RVdXJ0cHhkbVZ5YUtPOHBNTTlCZjBEd1RIIgogICAgICB9CiAgICAgIAogICAgfSwKICAgICJsaW1pdCI6IDEwCiAgfQp9">Here</a>
              You can see all the ratings of the address <code>bitcoincash:qqqdfw4md8zan9wf52809ycv7stlgrcycusmcc3drh</code>.<br>
              You can also see a simple mean of all of its ratings <a href="https://bitdb.bitcoin.">here</a>.<br>
              Obviously, a real reputation function would use a more elaborate aggregation function.
             </b-card>
          </b-collapse>

          <hr>

          <h2>Use cases</h2>
          <p>
            Straight simple use cases include matchmaking platforms (deBay, deUber, deAirBnb, ...), where merchants and users are rated as trustworthy, but other uses are possbile.<br>
            One example is anti-scalping measures: a band might decide to sell tickets only to legit digital identities with a track record of "normal" usage. This way bots can't frontrun everybody else and sell the tickets later at a premium.<br>
          </p>


          <h2>Future work</h2>
          <p>
            As of now, the system works with basic addresses and transactions, but it could probably be made to work with CashAccounts and CashIntents.
          </p>
          <p>
            This system is based on the publicity of the rating data. It could be a privacy concern for participants.<br>
            One can't have both perfect publicity AND confidentiality, but some degree of confidentiality can be achieved. For example the digital identities need not transact from their public addresses; instead they can sign a hashed OP_RETURN message that will only be revealed when a rating occurs. This way their interaction will still be public, but not in real-time.
          </p>
          <p>
            As of now, the rating system is purely p2p, there are no third parties present.<br>
            To meet, the digital identities will need some kind of market or matchmaking service (out of the scope of this document), that can optionally verify the realworld identity of the participants.<br>
            Also, the matchmaking service can verify that the transaction between the digital identities have met and transacted through its platform. Reputation services can choose to assign a different weight to matchmakers they trust more<br>
          </p>
          <p>
            Examples:<br>
            Coinbase already has your KYC. A user can ask Coinbase to verify (onchain) that their digital identity is tied to a realworld identity; the actual identity need not be released, just a certificate.<br>
            A user might prefer to transact with digital identities that are somehow verified. For example, I'd rather have a Uber drive from someone that is known to a trusted third party.<br>
            
          </p>

        </div>
      </div>

      <h1>Tutorial</h1>

        <p>
