                </div>
              </div>
            </div>
            <div class="col-6">
              <div class="row">
                <div class="col">
                  <div class="form-group">
                    <label for="seed1textarea">Create second wallet</label>
                    <textarea class="form-control" style="height: 100px;" placeholder="Paste mnemonic here or generate mnemonic" v-model="seed2"></textarea>
                  </div>
                  <button type="button" class="btn btn-primary" v-on:click="generateMnemonic('seed2')" :disabled="isNotEmpty(seed2)">Generate a mnemonic</button>&nbsp;
