                  <span v-if="wallet2">
                    <hr>
                    <p>Second wallet created! The default address is 
                      <code>{{getDefaultAddress(wallet2)}}</code>
                    </p>
                    <p> Balance: <code>{{wallet2balance}}</code>&nbsp;
                      <b-button variant="info" size="sm" v-on:click="updateBalance('wallet2')">refresh</b-button> 
                    </p>
                  </span>
                </div>
              </div>
            </div>
          </div>

          <div class="row" v-if="(typeof wallet1 !== 'undefined') && (typeof wallet2 !== 'undefined')">
            <div class="col text-center">
              <button class="btn btn-primary" v-on:click="register = true">I have saved my seeds in a safe place. Let me play.</button>&nbsp;
              <b-button class="btn btn-secondary" v-b-modal.modal-1>Send all funds to address</b-button>
            </div>
              <b-modal id="modal-1" title="Send funds to address">
                <div class="row">
                  <div class="col">
