            </div>
            <div class="col-6">
              <b-button block variant="success" v-on:click="registerIdentity('wallet1')">Register digital Identity on the first wallet</b-button>
              <b-form-textarea readonly v-model="wallet1RegisterOutput"></b-form-textarea>
            </div>
