              </p>
            </div>
              <div class="col-6">
                <p>Set a rating for the latest transaction from the left digital identity to the right</p>
                <div class="form-group">
                  <label for="left-rating">Insert rating (0 to 99)</label>
                  <input type="number" min="0" max="99" v-model="wallet1Rating" />
                </div>
                <b-button block variant="success" v-on:click="rateTransaction('wallet1')">Send Rating</b-button>
                <b-form-textarea readonly v-model="wallet1RatingOutput"></b-form-textarea>
              </div>
              <div class="col-6">
                <p>Set a rating for the latest transaction from the right digital identity to the left</p>
                <div class="form-group">
                  <label for="left-rating">Insert rating (0 to 99)</label>
                  <input type="number" min="0" max="99" v-model="wallet2Rating" />
                </div>
                <b-button block variant="success" v-on:click="rateTransaction('wallet2')">Send Rating</b-button>
                <b-form-textarea readonly v-model="wallet2RatingOutput"></b-form-textarea>
              </div>
          </div>

          <div class="row" v-if="wallet1Rated && wallet2Rated">
