              <b-button variant="primary" v-on:click="checkRatingTx('wallet2')">Decode the latest rating transaction from the right wallet</b-button>
              <b-button variant="primary" v-on:click="checkReputation('wallet2')">Check the average reputation for the right identity</b-button>
              <b-form-textarea style="min-height: 550px;" readonly v-model="wallet2CheckOutput"></b-form-textarea>
            </div>

