<script>
let BITBOX = require('bitbox-sdk').BITBOX
let bitbox = new BITBOX()
import * as libs from '../libs'

// libs.getRatingDetails('85e8a31f148fef6e7fdbf7685cd78b7cc5c8e58df3340bde1c246b2a5ac82ecf').then(console.log, console.log);



var seed1,seed2,
  wallet1, wallet2, register, wallet1balance, wallet2balance, wallet1Registered, wallet2Registered, wallet1RegisterOutput, wallet2RegisterOutput,
  transacted, transactionId, transactionOutput, wallet1Rating, wallet1Rated, wallet1RatingOutput, wallet2Rating, wallet2Rated, wallet2RatingOutput, toCashAddress, sendAllOutput,
  wallet1CheckOutput, wallet2CheckOutput;

export default {
  name: 'BitBox',
  data() {
    return {
      register: register,
      seed1: seed1,
      seed2: seed2,
      wallet1: wallet1,
      wallet2: wallet2,
      getDefaultAddress: function(wallet) {
        return bitbox.HDNode.toCashAddress(wallet);
      },
      wallet1balance: wallet1balance,
      wallet2balance: wallet2balance, 
      wallet1Registered: wallet1Registered,
      wallet2Registered: wallet2Registered,
      wallet1RegisterOutput: wallet1RegisterOutput,
      wallet2RegisterOutput: wallet2RegisterOutput,
      transacted: transacted,
      transactionId: transactionId,
      transactionOutput: transactionOutput,
      wallet1Rating: wallet1Rating,
      wallet1Rated: wallet1Rated,
      wallet1RatingOutput: wallet1RatingOutput,
      wallet2Rating: wallet2Rating,
      wallet2Rated: wallet2Rated,
      wallet2RatingOutput: wallet2RatingOutput,
      toCashAddress: toCashAddress,
      sendAllOutput: sendAllOutput,
      wallet1CheckOutput: wallet1CheckOutput,
      wallet2CheckOutput: wallet2CheckOutput
    }
  },
  methods: {
    isNotEmpty: function(string) {
      var empty = typeof string === 'undefined' || string === '';
      return !empty;
    },
    generateMnemonic: function (seed) {
      this[seed] = bitbox.Mnemonic.generate(256, bitbox.Mnemonic.wordLists()['english']);
    },
    updateBalance: function(wallet) {
      var self = this;
      bitbox.Address.details(bitbox.HDNode.toCashAddress(this[wallet])).then(
        function(result) {
          self[wallet+"balance"] = result.balance + result.unconfirmedBalance;
        }, function(err) {
          self[wallet+"balance"] = err;
      });
    },
    generateWallet: function (seed, wallet) {
      let rootSeed = bitbox.Mnemonic.toSeed(this[seed]);
      let masterHDNode = bitbox.HDNode.fromSeed(rootSeed, 'mainnet');
      this[wallet] = masterHDNode;
      this.updateBalance(wallet)
    },
    registerIdentity: function(wallet) {
      var self = this;
      self[wallet+"RegisterOutput"] = "loading"
      libs.claimAddress(this[wallet]).then(function(response) {
        self[wallet+"RegisterOutput"] = "Identity registered in transaction " + response;
        self[wallet+"Registered"] = true;
      }, function (err) {
        self[wallet+"RegisterOutput"] = err
      })
    },
    makeTransaction: function() {
      var self = this;
      self.transactionOutput = 'loading';
      libs.transact(this.wallet1, this.wallet2, 1000).then(
        function(response) {
          self.transactionId = response;
          self.transactionOutput = "Sent successfully in transaction "+ response;
          self.transacted = true;
        }, function(error) {
          self.transactionOutput = error;
        }
      )
    },
    rateTransaction: function(wallet) {
      var otherWallet = wallet === 'wallet1' ? 'wallet2' : 'wallet1';
      var self = this;
      self[wallet+"RatingOutput"] = "loading";
      libs.rateTransaction(
        bitbox.HDNode.toCashAddress(this[otherWallet]),
        bitbox.HDNode.toCashAddress(this[wallet]),
        this.transactionId,
        this[wallet+"Rating"],
        this[wallet]
      ).then(
        function(response) {
          self[wallet+"RatingOutput"] = response;
          self[wallet+"Rated"] = true;
        }, function(error) {
          self[wallet+"RatingOutput"] = error;
        }
      )
    },
    sendAllToAddress: function() {
      var self = this;
      if (bitbox.Address.isCashAddress(this.toCashAddress)) {
        libs.sendAllToCashAddress(this.wallet1, this.toCashAddress).then(
          function(response) {
            self.sendAllOutput += "Left wallet output " + response + "<br>";
          }, function(error) {
            self.sendAllOutput += "Left wallet output " + error;
          }
        );
        libs.sendAllToCashAddress(this.wallet2, this.toCashAddress).then(
          function(response) {
            self.sendAllOutput += "Right wallet output " + response + "<br>";
          }, function(error) {
            self.sendAllOutput += "Right wallet output " + error;
          }
        );
      } else {
        this.sendAllOutput = "Not a valid cashaddress."
      }
    },
    checkRatingTx: function(wallet) {
      var self = this;
      self[wallet+"CheckOutput"] = "loading";
      libs.getRatingDetails(self[wallet+"RatingOutput"]).then(
        function(result) {
          self[wallet+"CheckOutput"] = JSON.stringify(result, null, 2);
        }, function(error) {
          self[wallet+"CheckOutput"] = error;
        }
      )
    },
    checkReputation: function(wallet) {
      var self = this;
      self[wallet+"CheckOutput"] = "loading";
      libs.getSimpleAverage(this[wallet]).then(
        function(response) {
          self[wallet+"CheckOutput"] = JSON.stringify(response, null, 2);
        }, function(error) {
          self[wallet+"CheckOutput"] = error;
        }
      )
    }
  }
}
</script>

